












































































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import moment from 'moment';
import MomentUtil from '@/typescript/MomentUtil.ts';
import CSelectorYMD from '@/components/CSelectorYMD.vue';
import CSelectorHMS from '@/components/CSelectorHMS.vue';
import CDatetime from '@/components/links/CDatetime.vue';

@Component({
  components: {
    CSelectorYMD,
    CSelectorHMS,
    CDatetime,
  },
  head: {
    title: {
      inner: 'カウントダウン',
    },
  },
})
export default class CountDown extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataYearTerms: {
    start: number,
    end: number,
  } = {
    start: 0,
    end: 120,
  };
  private dataNowMoment: any = moment(new Date());
  private dataEventPoint: {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
    millisecond: number,
  } = {
    year: 0,
    month: 0,
    day: 0,
    hour: 0,
    minute: 0,
    second: 0,
    millisecond: 0,
  };
  private dataEachPointDiff: {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
    millisecond: number,
  } = {
    year: 0,
    month: 0,
    day: 0,
    hour: 0,
    minute: 0,
    second: 0,
    millisecond: 0,
  };
  private dataTotalPointDiff: {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
    millisecond: number,
  } = {
    year: 0,
    month: 0,
    day: 0,
    hour: 0,
    minute: 0,
    second: 0,
    millisecond: 0,
  };
  private dataAlert: string = '';

  @Watch('dataNowMoment')
  onNowMomentChange(newValue: string, oldValue: string): void {
    this.diffDatetime();
  }

  // Lifecycle hook
  created(): void {
    this.dataEventPoint = MomentUtil.toDatePoint(this.dataNowMoment.clone().endOf('year'));
    this.getNow();
  }

  // Methods
  private getNow(): void {
    setInterval((): void => {
      let now: any = moment(new Date());
      if (this.dataNowMoment !== now) {
        this.dataNowMoment = now;
      }
    }, 500);
  }
  private calcNaturalMonth(month: number): number {
    return month + 1;
  }
  private diffDatetime(): void {
    let nowMoment: any = this.dataNowMoment.clone();
    let eventMoment: any = MomentUtil.toDateMoment(this.dataEventPoint);
    if (eventMoment.diff(nowMoment) < 0) {
      this.dataAlert = 'すでにイベント日時に到達しています。';
    } else {
      this.dataAlert = '';
      this.dataEachPointDiff = MomentUtil.diffDateMomentsToEachPointDiff(nowMoment, eventMoment);
      this.dataTotalPointDiff = MomentUtil.diffDateMomentsToTotalPointDiff(nowMoment, eventMoment);
    }
  }
  private listenEventPointYearChange($event: Event): void {
    Vue.set(this.dataEventPoint, 'year', Number($event));
  }
  private listenEventPointMonthChange($event: Event): void {
    Vue.set(this.dataEventPoint, 'month', Number($event));
  }
  private listenEventPointDayChange($event: Event): void {
    Vue.set(this.dataEventPoint, 'day', Number($event));
  }
  private listenEventPointHourChange($event: Event): void {
    Vue.set(this.dataEventPoint, 'hour', Number($event));
  }
  private listenEventPointMinuteChange($event: Event): void {
    Vue.set(this.dataEventPoint, 'minute', Number($event));
  }
  private listenEventPointSecondChange($event: Event): void {
    Vue.set(this.dataEventPoint, 'second', Number($event));
  }
}
